import React from "react";
import { Raw } from "types";
import { Select } from "antd";
//获取到select的所有属性
type SelectProps = React.ComponentProps<typeof Select>;
//继承select中除了（'value' | 'onchange' | 'options'）以外的所有属性
interface IdSelcetProps
  extends Omit<SelectProps, "value" | "onChange" | "options"> {
  value?: Raw | null | undefined;
  onChange?: (value?: number) => void;
  defaultOptionName?: string;
  options?: { name: string; id: number }[];
}

/**
 * value 可以传入多种类型的值
 * onChange只会回调 number|undefined 类型
 * 当 isNaN(Number(value)) 为true的时候，代表选择默认类型
 * 当选择默认类型的时候，onChange会回调undefined
 * @param props
 * @constructor
 */

export const IdSelect = (props: IdSelcetProps) => {
  const { value, onChange, defaultOptionName, options, ...restProps } = props;
  return (
    <Select
      value={options?.length ? toNumber(value) : 0}
      onChange={(value) => onChange?.(toNumber(value) || undefined)}
      {...restProps}
    >
      {
        /*如果有默认选项 */
        defaultOptionName ? (
          <Select.Option value={0}>{defaultOptionName}</Select.Option>
        ) : null
      }
      {options?.map((option: { name: string; id: number }) => (
        <Select.Option key={option.id} value={option.id}>
          {option.name}
        </Select.Option>
      ))}
    </Select>
  );
};

//因为虽然后端传入的id是数字，但在select中会转换成string，所以如果value是能转换成数字的，就转换成数字，否则变成默认值0（代表无意义）
const toNumber = (value: unknown) => {
  return isNaN(Number(value)) ? 0 : Number(value);
};
